  readonly vpcSubnets?: ec2.SubnetSelection;

                                                                                                                       
  readonly securityGroups?: ec2.ISecurityGroup[];

                                                                                                        
  readonly port?: number;

                                                                                                               
  readonly parameterGroup?: IParameterGroup;

                                                                                                      
  readonly optionGroup?: IOptionGroup;

                                                                                                                                                   
