  readonly enablePerformanceInsights?: boolean;

                                                                                                       
  readonly performanceInsightRetention?: PerformanceInsightRetention;

                                                                                                                     
  readonly performanceInsightEncryptionKey?: kms.IKey;

                                                                                                                                         
  readonly cloudwatchLogsExports?: string[];

                                                                                                                                                                                                                                                                            
