  readonly maxAllocatedStorage?: number;

                                                                                                                        
  readonly domain?: string;

                                                                                                                                                                                                                                                                                                
  readonly domainRole?: iam.IRole;

                                                                                                                 
  readonly subnetGroup?: ISubnetGroup;

                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    
